import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Start from '@/components/Start'

Vue.use(Router)

export default new Router({
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/start',
      name: 'Start',
      component: Start
    },
    {
		path: '*',
		redirect: '/',
	},
  ]
})



